import React from 'react'
import Img from 'react-image'
import YouTube from 'react-youtube'
import Sticky from 'react-stickynode'
import { NavHashLink as NavLink } from 'react-router-hash-link'
import logo from '../assets/images/calvano-logo-updated.png'


// TODO: pause video when video is out of viewport
export default () => {
  const opts = {
      height: '1',
      width: '1',
      playerVars: { // https://developers.google.com/youtube/player_parameters
        autoplay: 1,
        autohide: 1,
        modestbranding: 0,
        rel: 0,
        showinfo: 0,
        controls: 0,
        disablekb: 1,
        enablejsapi: 0,
        iv_load_policy: 3,
        loop: 1,
        width: "100%",
        height: "100%",
        frameborder: 0,
        playlist: "JQwM1XkkGN8",
        webkitallowfullscreen: true,
        mozallowfullscreen: true,
        allowfullscreen: true
      }
    };
  return (
    <header>
      <YouTube
        videoId="JQwM1XkkGN8"
        opts={opts}
        containerClassName="banner-video"
      />
      <h1>
        <span>±23,372 RSF New Construction</span>
        <span>1075 Terra Bella Avenue, Mountain View, CA</span>
      </h1>
      <Sticky enabled={true} top={"#nav"} activeClass="sticky-nav" releasedClass="not-sticky">
        {/* TODO: figure out how to add offset */}
        <nav id="nav">
          <a href="https://www.calvano.com" target="_blank" rel="noreferrer noopener"><Img src={logo} alt="" /></a>
          <ul>
            <NavLink smooth to="#virtual-gallery"><li>360° Tour</li></NavLink>
            <NavLink smooth to="#gallery"><li>Gallery</li></NavLink>
            <NavLink smooth to="#employee-experience"><li>Employee Experience</li></NavLink>
            <NavLink smooth to="#floor-plans"><li>Floor Plans</li></NavLink>
            <NavLink smooth to="#garden-path"><li>Garden Path</li></NavLink>
            <NavLink smooth to="#map"><li>Map</li></NavLink>
            <NavLink smooth to="#contact"><li>Contact</li></NavLink>
          </ul>
        </nav>
      </Sticky>
    </header>
  )
}



