import React from 'react'
import ImageGallery from 'react-image-gallery'
import { Container, Row, Col } from 'reactstrap'
import SectionHeading from '../components/SectionHeading'
import floor1Office from '../assets/images/gallery/floor1-office.jpg'
import floor1OfficeThumb from '../assets/images/gallery/floor1-office_tn.jpg'
import floor1Tech from '../assets/images/gallery/floor1-tech.jpg'
import floor1TechThumb from '../assets/images/gallery/floor1-tech_tn.jpg'
import floor2Balcony from '../assets/images/gallery/floor2-balcony.jpg'
import floor2BalconyThumb from '../assets/images/gallery/floor2-balcony_tn.jpg'
import floor2Office from '../assets/images/gallery/floor2-office.jpg'
import floor2OfficeThumb from '../assets/images/gallery/floor2-office_tn.jpg'
import floor2OfficeTech from '../assets/images/gallery/floor2-office-tech.jpg'
import floor2OfficeTechThumb from '../assets/images/gallery/floor2-office-tech_tn.jpg'
import floor2Tech from '../assets/images/gallery/floor2-tech.jpg'
import floor2TechThumb from '../assets/images/gallery/floor2-tech_tn.jpg'
import floor2Tech2 from '../assets/images/gallery/floor2-tech2.jpg'
import floor2Tech2Thumb from '../assets/images/gallery/floor2-tech2_tn.jpg'
import lobby from '../assets/images/gallery/lobby.jpg'
import lobbyThumb from '../assets/images/gallery/lobby_tn.jpg'
import outsideBack from '../assets/images/gallery/outside-back.jpg'
import outsideBackThumb from '../assets/images/gallery/outside-back_tn.jpg'
import outsideFront from '../assets/images/gallery/outside-front.jpg'
import outsideFrontThumb from '../assets/images/gallery/outside-front_tn.jpg'

const Gallery = (props) => {

  const images = [
    {
      original: outsideFront,
      thumbnail: outsideFrontThumb,
    },
    {
      original: outsideBack,
      thumbnail: outsideBackThumb,
    },
    {
      original: floor1Office,
      thumbnail: floor1OfficeThumb,
    },
    {
      original: floor1Tech,
      thumbnail: floor1TechThumb,
    },
    {
      original: lobby,
      thumbnail: lobbyThumb,
    },
    {
      original: floor2Office,
      thumbnail: floor2OfficeThumb,
    },
    {
      original: floor2Tech,
      thumbnail: floor2TechThumb,
    },
    {
      original: floor2Balcony,
      thumbnail: floor2BalconyThumb,
    },
    {
      original: floor2Tech2,
      thumbnail: floor2Tech2Thumb,
    },
    {
      original: floor2OfficeTech,
      thumbnail: floor2OfficeTechThumb,
    },
  ]
  return (
    <section id="gallery" className="gallery">
      <Container fluid>
        <Row>
          <Col lg={{size: 10, offset: 1}}>
            <SectionHeading>Exterior Gallery</SectionHeading>
            <ImageGallery items={images} lazyLoad />
          </Col>
        </Row>
      </Container>
    </section>
  )
}

export default Gallery;



