import React from 'react'
import { Container, Row, Col } from 'reactstrap'
import SectionHeading from '../components/SectionHeading'
import brettPhoto from '../assets/images/brett.png'
import davePhoto from '../assets/images/dave.png'
import calvanoLogo from '../assets/images/calvano-logo-dark-text.png'
import colliersLogo from '../assets/images/colliers-logo.png'

const Footer = (props) => (
    <section id="contact" className="footer">
      <Container>
        <SectionHeading>Contact</SectionHeading>
        <Row>
          <Col md={{size: 4, offset: 4}}>
            <img className="calvano-logo" src={calvanoLogo} alt="" />
          </Col>
        </Row>
        <Row>
          <Col md="4">
            <div className="contact">
              <img src={davePhoto} alt="" />
              <h3>Dave Sandlin</h3>
              <address>
                Executive Vice President<br />
                +1 408 282 3988<br />
                Lic. No. 00767849
              </address>
            </div>
          </Col>
          <Col md="4">
            <div className="contact">
              <img src={brettPhoto} alt="" />
              <h3>Brett Taylor</h3>
              <address>
                Senior Vice President<br />
                +1 650 492 5233<br />
                Lic. No. 01856608
              </address>
            </div>
          </Col>
          <Col md="4">
            <div className="contact">
              <img src={colliersLogo} alt="" />
              <h3>Colliers International</h3>
              <address>
                450 West Santa Clara<br />
                +1 650 492 5233<br />
                Lic. No. 01856608
              </address>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
)

export default Footer



