import React, { Component } from 'react'
import Homepage from './pages/Homepage'
import { BrowserRouter } from 'react-router-dom'
import './App.css'

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <Homepage />
      </BrowserRouter>
    );
  }
}

export default App;



